
import axios from "axios";
import { createContext, useContext, useState, ReactNode } from "react";

type Book = {
  id: string;
  title: string;
  author?: string;
  cover?: string;
  infoLink?: string;
};

type BooksContextType = {
  books: Book[];
  fetchBooks: (query: string) => Promise<void>;
  addFavorite: (book: Book) => Promise<void>;
};

const BooksContext = createContext<BooksContextType | undefined>(undefined);

export const BooksProvider = ({ children }: { children: ReactNode }) => {
  const [books, setBooks] = useState<Book[]>([]);

  const fetchBooks = async (query: string) => {
    const res = await axios.get(`/api/books?query=${query}`);
    setBooks(res.data);
  };

  const addFavorite = async (book: Book) => {
    try {
      const token = localStorage.getItem("token");
      if (!token) throw new Error("Usuário não logado");

      await axios.post(
        `/api/favorites/${book.id}`,
        {}, // body vazio
        { headers: { Authorization: `Bearer ${token}` } }
      );

      alert(`${book.title} adicionado aos favoritos!`);
    } catch (err: any) {
      console.error(err);
      alert("Não foi possível favoritar o livro.");
    }
  };

  return (
    <BooksContext.Provider value={{ books, fetchBooks, addFavorite }}>
      {children}
    </BooksContext.Provider>
  );
};

export const useBooks = () => {
  const ctx = useContext(BooksContext);
  if (!ctx) throw new Error("useBooks deve estar dentro de BooksProvider");
  return ctx;
};
